import React, { useState } from "react";
import Lottie from "react-lottie";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import ButtonArrow from "./ui/ButtonArrow";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import { Link } from "react-router-dom";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import Snackbar from "@material-ui/core/Snackbar";
import CallToAction from "./ui/CallToAction";
import animationData from "./../animations/landinganimation/data";
import customSoftwareicon from "./../assets/Custom Software Icon.svg";
import mobileAppsIcon from "./../assets/mobileIcon.svg";
import websiteIcon from "./../assets/websiteIcon.svg";
import revolutionBackground from "./../assets/repeatingBackground.svg";
import infoBackground from "./../assets/infoBackground.svg";
import firstcoverimage from "./../logistic_assets/cover_image_1.png";
import secondcoverimage from "./../logistic_assets/cover_image2.png";
import boathaulage from "./../logistic_assets/boat_haulage.png";
import boxtruck from "./../logistic_assets/box_truck.png";
import carcarriertrailer from "./../logistic_assets/Car_Carrier_Trailer.png";
import cementtruck from "./../logistic_assets/Cement_Truck.png";
import chillertruck from "./../logistic_assets/Chiller_Trucks.png";
import flatbedtrailer from "./../logistic_assets/Flat_bed_Trailer.png";
import livestocktruck from "./../logistic_assets/Livestock_Trucks.png";
import loggingtruck from "./../logistic_assets/Logging_Trucks.png";
import tanker from "./../logistic_assets/Tankers.png";
import tippertruck from "./../logistic_assets/Tipper_Trucks.png";
import towtruck from "./../logistic_assets/Tow_Truck.png";
import cranetruck from "./../logistic_assets/crane truck2.png";
import ProductCard from "./ProductCard";
import background from "./../logistic_assets/cover_image_1.png";
import { Category } from "@material-ui/icons";
import history from "../history";
import AboutUsFormContainer from "./aboutus/AboutUsFormContainer";
import ContactUsContainerForm from "./contactus/ContactUsContainerForm";
import BecomePartnerFormContainer from "./partner/BecomePartnerFormContainer";
//import mobileBackground from "./../../assets/mobileBackground.jpg";

const useStyles = makeStyles((theme) => ({
  animation: {
    // maxWidth: "100em",
    minWidth: "21em",
    marginTop: "2em",
    marginLeft: "10%",
    [theme.breakpoints.down("sm")]: {
      maxWidth: "30em",
    },
  },
  estimateButton: {
    ...theme.typography.estimate,
    backgroundColor: theme.palette.common.orange,
    borderRadius: 50,
    height: 45,
    width: 155,
    marginRight: 40,
    fontWeight: 400,
    "&:hover": {
      backgroundColor: theme.palette.secondary.light,
    },
  },
  buttonContainer: {
    marginTop: "1em",
  },
  learnButtonHero: {
    ...theme.typography.learnButton,
    fontSize: "0.7rem",
    height: 45,
    width: 145,
  },
  visitPartnerButtonsite: {
    ...theme.typography.partnerButton,
    fontSize: "0.9rem",
    height: 45,
    width: 200,
    "&:hover": {
      backgroundColor: theme.palette.common.white,
    },
  },
  learnButton: {
    ...theme.typography.learnButton,
    fontSize: "0.7rem",
    height: 35,
    padding: 5,
    border: `2px solid ${theme.palette.common.blue}`,
    [theme.breakpoints.down("sm")]: {
      marginBottom: "2em",
    },
  },
  mainContainer: {
    marginTop: "5em",
    marginLeft: "2px",
    [theme.breakpoints.down("md")]: {
      marginTop: "3em",
    },
    [theme.breakpoints.down("xs")]: {
      marginTop: "2em",
    },
  },
  heroTextContainer: {
    minWidth: "21.5em",
    marginLeft: "1em",
    color: "white",
    [theme.breakpoints.down("xs")]: {
      marginLeft: 0,
    },
  },
  specialText: {
    fontFamily: "Pacifico",
    color: theme.palette.common.orange,
  },
  subtitle: {
    marginBottom: "1em",
  },
  icon: {
    marginLeft: "2em",
    [theme.breakpoints.down("xs")]: {
      marginLeft: 0,
    },
  },
  serviceContainer: {
    marginTop: "12em",
    [theme.breakpoints.down("sm")]: {
      padding: 25,
    },
  },
  revolutionBackground: {
    backgroundImage: `url(${revolutionBackground})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    height: "100%",
    width: "100%",
  },
  revolutionCard: {
    position: "absolute",
    boxShadow: theme.shadows[10],
    borderRadius: 15,
    padding: "10em",
    [theme.breakpoints.down("sm")]: {
      paddingTop: "8em",
      paddingBottom: "8em",
      paddingLeft: 0,
      paddingRight: 0,
      borderRadius: 0,
      width: "100%",
    },
  },
  infoBackground: {
    backgroundImage: `url(${infoBackground})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    height: "100%",
    width: "100%",
  },

  background: {
    backgroundImage: `url(${background})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
    //backgroundAttachment: "fixed",
    backgroundRepeat: "no-repeat",
    height: "60em",
    width: "100%",
    [theme.breakpoints.down("md")]: {
      // backgroundImage: `url(${mobileBackground})`,
      backgroundAttachment: "inherit",
    },
  },
}));

const Marketplace = (props) => {
  const classes = useStyles();
  const theme = useTheme();
  const matchesSM = useMediaQuery(theme.breakpoints.down("sm"));
  const matchesXS = useMediaQuery(theme.breakpoints.down("xs"));
  const [aboutUsOpen, setAboutUsOpen] = useState(false);
  const [contactUsOpen, setContactUsOpen] = useState(false);
  const [becomePartnerOpen, setBecomePartnerOpen] = useState(false);
  const [alert, setAlert] = useState({
    open: false,
    message: "",
    backgroundColor: "",
  });
  const defaultOptions = {
    loop: true,
    autoplay: false,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidyMid slice",
    },
  };

  const handleBecomeAPartnerOpenDialogBox = () => {
    setBecomePartnerOpen(false);
  };

  const handleSuccessfulBecomeAPartnerOpenDialogBoxWithSnackbar = () => {
    setBecomePartnerOpen(false);
    setAlert({
      open: true,
      message: "Application successfully submitted",
      backgroundColor: "#4BB543",
    });
  };

  const handleFailedBecomeAPartnerOpenDialogBoxWithSnackbar = () => {
    setAlert({
      open: true,
      message: "Something went wrong somewhere",
      backgroundColor: "#FF3232",
    });
    setBecomePartnerOpen(true);
  };

  const categories = [
    {
      title: "Flat-bed Trailer",
      description: "This is the Crane Trucks/Mobile Cranes",
      image: flatbedtrailer,
      alt: "Flat-bed Trailer",
    },
    {
      title: "Crane Trucks/Mobile Cranes",
      description: "This is the Flat-Bed Trailers",
      image: cranetruck,
      alt: "Crane Trucks",
    },
    {
      title: "Tow Truck",
      description: "This is the Tow Truck",
      image: towtruck,
      alt: "Tow Truck",
    },
    {
      title: "Tankers",
      description: "This is the Tankers",
      image: tanker,
      alt: "Tankers",
    },
    {
      title: "Tipper Trucks",
      description: "This is the Tipper Trucks",
      image: tippertruck,
      alt: "Tipper Trucks",
    },
    {
      title: "Logging Trucks",
      description: "This is the Logging Trucks",
      image: loggingtruck,
      alt: "Logging Trucks",
    },
    {
      title: "Car Carrier Trailer",
      description: "This is the Car Carrier Trailer",
      image: carcarriertrailer,
      alt: "Car Carrier Trailer",
    },
    {
      title: "Cement Truck",
      description: "This is the Cement Truck",
      image: cementtruck,
      alt: "Cement Truck",
    },
    {
      title: "Boat Haulage",
      description: "This is the Boat Haulage",
      image: boathaulage,
      alt: "Boat Haulage",
    },
    {
      title: "Chiller Trucks/Reefers",
      description: "This is the Chiller Trucks/Reefers",
      image: chillertruck,
      alt: "Chiller Trucks/Reefers",
    },
    {
      title: "Box Truck",
      description: "This is the Box Truck",
      image: boxtruck,
      alt: "Box Truck",
    },
    {
      title: "Livestock Trucks",
      description: "This is the Livestock Trucks",
      image: livestocktruck,
      alt: "Livestock Trucks",
    },
  ];

  const categoriesList = (
    <React.Fragment>
      <Grid container direction="row">
        {categories.map((category, index) => (
          <ProductCard
            title={category.title}
            key={`${category.title}${index}`}
            description={category.description}
            image={category.image}
            token={props.token}
            userId={props.userId}
          />
        ))}
      </Grid>
    </React.Fragment>
  );

  return (
    <Grid container direction="row" className={classes.mainContainer}>
      <Grid
        container
        alignItems="center"
        className={classes.background}
        justifyContent={matchesSM ? "center" : "space-between"}
        direction={matchesSM ? "column" : "row"}
        style={{ marginTop: -100 }}
      >
        <Grid item>
          {" "}
          {/*..... HERO BLOCK.... */}
          <Grid
            container
            justifyContent="flex-end"
            alignItems="center"
            direction="row"
          >
            <Grid sm item className={classes.heroTextContainer}>
              <Typography variant="h2" align="center">
                We are Africa's leading Logistics <br /> Marketplace
              </Typography>
              <Grid
                container
                justifyContent="center"
                className={classes.buttonContainer}
              >
                <Grid item>
                  <Button
                    // component={Link}
                    // to="/estimate"
                    className={classes.estimateButton}
                    variant="contained"
                    onClick={() => [
                      setBecomePartnerOpen(true),
                      history.push("/"),
                    ]}
                  >
                    Become a Partner
                  </Button>
                </Grid>
                <Grid item>
                  <Button
                    // component={Link}
                    // to="/revolution"
                    variant="outlined"
                    className={classes.visitPartnerButtonsite}
                    onClick={(event) => {
                      event.preventDefault();
                      //  history.push(`/orders/completed`);
                      window.open("http://localhost:3009/");
                    }}
                  >
                    <span style={{ marginRight: 10 }}>Visit Partner Site </span>
                    <ButtonArrow
                      width={15}
                      height={15}
                      fill={theme.palette.common.orange}
                    />
                  </Button>
                </Grid>
              </Grid>
            </Grid>
            {/* <Grid sm item className={classes.animation}>
            <Lottie options={defaultOptions} height={"100%"} width={"100%"} />
          </Grid> */}
          </Grid>
        </Grid>
      </Grid>

      <Grid item>{categoriesList}</Grid>

      <Grid item style={{ width: "100%", marginTop: "20px" }}>
        {" "}
        {/*....INFORMATION BLOCK....*/}
        <Grid
          container
          direction="row"
          alignItems="center"
          style={{ height: "80em" }}
          className={classes.infoBackground}
        >
          <Grid
            container
            style={{
              textAlign: matchesXS ? "center" : "inherit",
            }}
            direction={matchesSM ? "column" : "row"}
          >
            <Grid
              item
              sm
              style={{ marginLeft: matchesXS ? 0 : matchesSM ? "2em" : "5em" }}
            >
              <Grid
                container
                direction="column"
                style={{ marginBottom: matchesXS ? "10em" : 0 }}
              >
                <Typography variant="h2" style={{ color: "white" }}>
                  About Us
                </Typography>
                <Typography variant="subtitle2">Let's get personal</Typography>
                <Grid item>
                  <Button
                    // component={Link}
                    // to="/about"
                    varaint="outlined"
                    className={classes.learnButton}
                    onClick={() => [setAboutUsOpen(true), history.push("/")]}
                    style={{ color: "white", borderColor: "white" }}
                  >
                    <span style={{ marginRight: 10 }}>Learn More </span>
                    <ButtonArrow height={10} width={10} fill="white" />
                  </Button>
                </Grid>
                <Dialog
                  //style={{ zIndex: 1302 }}
                  fullScreen={matchesXS}
                  open={aboutUsOpen}
                  onClose={() => [setAboutUsOpen(false), history.push("/")]}
                  fullWidth
                  maxWidth="md"
                >
                  <DialogContent>
                    <AboutUsFormContainer
                      token={props.token}
                      // handleDialogOpenStatus={handleDialogOpenStatus}
                    />
                  </DialogContent>
                </Dialog>
              </Grid>
            </Grid>

            <Grid
              item
              sm
              style={{
                marginRight: matchesXS ? 0 : matchesSM ? "2em" : "5em",
                textAlign: matchesXS ? "center" : "right",
              }}
            >
              <Grid container direction="column">
                <Typography variant="h2" style={{ color: "white" }}>
                  Contact Us
                </Typography>
                <Typography variant="subtitle2">Say hello!</Typography>
                <Grid item>
                  <Button
                    // component={Link}
                    // to="/contact"
                    varaint="outlined"
                    className={classes.learnButton}
                    style={{ color: "white", borderColor: "white" }}
                    onClick={() => [setContactUsOpen(true), history.push("/")]}
                  >
                    <span style={{ marginRight: 10 }}>Learn More </span>
                    <ButtonArrow height={10} width={10} fill="white" />
                  </Button>
                </Grid>
                <Dialog
                  //style={{ zIndex: 1302 }}
                  fullScreen={matchesXS}
                  open={contactUsOpen}
                  onClose={() => [setContactUsOpen(false), history.push("/")]}
                >
                  <DialogContent>
                    <ContactUsContainerForm
                      token={props.token}
                      // handleDialogOpenStatus={handleDialogOpenStatus}
                    />
                  </DialogContent>
                </Dialog>
                <Dialog
                  //style={{ zIndex: 1302 }}
                  fullScreen={matchesXS}
                  open={becomePartnerOpen}
                  onClose={() => [
                    setBecomePartnerOpen(false),
                    history.push("/"),
                  ]}
                >
                  <DialogContent>
                    <BecomePartnerFormContainer
                      token={props.token}
                      userId={props.userId}
                      handleSuccessfulBecomeAPartnerOpenDialogBoxWithSnackbar={
                        handleSuccessfulBecomeAPartnerOpenDialogBoxWithSnackbar
                      }
                      handleFailedBecomeAPartnerOpenDialogBoxWithSnackbar={
                        handleFailedBecomeAPartnerOpenDialogBoxWithSnackbar
                      }
                    />
                  </DialogContent>
                </Dialog>
                <Snackbar
                  open={alert.open}
                  message={alert.message}
                  ContentProps={{
                    style: { backgroundColor: alert.backgroundColor },
                  }}
                  anchorOrigin={{ vertical: "top", horizontal: "center" }}
                  onClose={() => setAlert({ ...alert, open: false })}
                  autoHideDuration={4000}
                />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item style={{ width: "100%" }}>
        {/*....CALL TO ACTION BLOCK ....*/}
        <CallToAction
          setValue={props.setValue}
          token={props.token}
          userId={props.userId}
          handleSuccessfulBecomeAPartnerOpenDialogBoxWithSnackbar={
            handleSuccessfulBecomeAPartnerOpenDialogBoxWithSnackbar
          }
          handleFailedBecomeAPartnerOpenDialogBoxWithSnackbar={
            handleFailedBecomeAPartnerOpenDialogBoxWithSnackbar
          }
        />
      </Grid>
    </Grid>
  );
};

export default Marketplace;
